.loader{
  position:fixed;
  width:100%;
  height:100%;
  left:0;
  top:0;
  background:#fff;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align:center;
      align-items:center;
  opacity: 1;
  -webkit-transition:.6s all;
  -o-transition:.6s all;
  transition:.6s all;
  z-index:-1;
}

.loader.loader-active{
  opacity:1;
  z-index:12;
}

.loader img{
  display:block;
  width:100px;
  -webkit-transform:translate(-50%,-50%);
      -ms-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  -webkit-animation: loaderAnim 2s infinite alternate;
          animation: loaderAnim 2s infinite alternate;
}


@-webkit-keyframes loaderAnim {
  0%{
    -webkit-transform:scale(0.6) ;
            transform:scale(0.6) ;
  }
  100%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }

}


@keyframes loaderAnim {
  0%{
    -webkit-transform:scale(0.6) ;
            transform:scale(0.6) ;
  }
  100%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }

}


